import { useEffect, useState } from 'react';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';
import { Grid, Link, Typography } from '@mui/material';
import { UUID } from 'crypto';

function App() {

  type AAGuidInfo = {
    description: string,
    aaguid: UUID
    authenticatorVersion: number
    protocolFamily: string
  }

  const [data, setData] = useState<AAGuidInfo[]>([]);

  useEffect(() => {
    fetch('mdsblob.json')
      .then(response => response.json())
      .then(data => data.map((entry: AAGuidInfo) => ({ id: entry.aaguid, name: entry.description })))
      .then(setData)
      .catch(console.error);
  }, [])

  const columns = [
    { field: 'name', headerName: 'Name', flex: 1 },
    { field: 'id', headerName: 'AAGUID', flex: 1 }
  ];

  return (
    <Grid
      container
      spacing={1}
      direction="column"
      alignItems="center"
      justifyContent="center"
    >
      <Typography variant="h1" component="h2">
        AAGUIDs
      </Typography>
      <Typography>
        AAGUIDs are the unique identifiers for passkey authenticators. They are used to identify the authenticator when using the WebAuthn API.
      </Typography>
      <Typography>
        This list is based on the <Link href="https://github.com/passkeydeveloper/passkey-authenticator-aaguids/blob/main/combined_aaguid.json">FIDO Alliance Metadata Service</Link> and is updated regularly.
      </Typography>

      <Grid item>
        <DataGrid
          loading={data.length === 0}
          rows={data}
          columns={columns}
          initialState={{
            sorting: {
              sortModel: [{ field: 'name', sort: 'asc' }],
            },
          }}
          slots={{ toolbar: GridToolbar }}
          slotProps={{
            toolbar: {
              showQuickFilter: true,
            },
          }}
        />
      </Grid>
    </Grid>
  );
}

export default App;
